/*    */

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: opacity 300ms ease-in-out, visibility 300ms ease-in-out;
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*      */
  width: 280px;
  @include tablet {
    width: 500px;
  }

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 28px;
  background-color: $secondary-font-color;
  border: 2px solid $accent-color;
  border-radius: 10px;
  transition: opacity 300ms ease-in-out, visibility 300ms ease-in-out;

  @include tablet {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

/*    */
.modal__title {
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 1.5;
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

/*     */

.modal__cross {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 16px;
  right: 16px;
  fill: $primary-font-color;
  cursor: pointer;
  transition: fill 300ms ease-in-out;

  &:hover,
  &:focus {
    fill: $accent-color;
  }
}
